import React from 'react';

export default props => {
  return (
    <div
      style={{display: props.show }}
      className="modal-bg"
      onClick={e => {
        e.stopPropagation();
        if (props.closer) props.setShow('none');
      }}
    >
      <div className="modal-box">
        <div className="modal-content">{props.children}</div>
      </div>
    </div>
  );
};
